<template>
  <div class="home">
    <loading v-if="loading" />
    <div class="products" v-if="products">
      <div v-for="product in products" :key="product.id">
        <product-card :product="product" />
      </div>
      <product-modal
        :productId="this.$store.state.productId"
        v-if="this.$store.state.modalActive"
        v-on:close-modal="closeModal"
      />
    </div>
  </div>
</template>

<script>
import Loading from "../components/Loading.vue";
import ProductCard from "../components/ProductCard.vue";
import ProductModal from "../components/ProductModal.vue";

export default {
  components: { ProductCard, ProductModal, Loading },
  name: "Home",

  data() {
    return {
      loading: null,
      products: null,
      modalActive: false,
    };
  },
  watch: {
    $route() {
      this.fetchProducts();
    },
  },
  mounted() {
    this.fetchProducts();
  },

  methods: {
    fetchProducts() {
      this.loading = true;
      this.axios
        .get("products?sort=desc")
        // .get(`${this.$route.fullPath.replace("/search", "/products")}`)
        .then((data) => {
          this.products = data.data;
          console.log(data.data);
          this.loading = false;
        });
    },
    closeModal() {
      this.$store.state.modalActive = !this.$store.state.modalActive;
    },
  },
};
</script>
<style lang="scss" scoped>
.products {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}
</style>
